@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,300&display=swap');

@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?ijbnf0');
  src:  url('fonts/icomoon.eot?ijbnf0#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?ijbnf0') format('truetype'),
    url('fonts/icomoon.woff?ijbnf0') format('woff'),
    url('fonts/icomoon.svg?ijbnf0#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

